( function( $ ) {
	/* global rtsbWishlistParams, rtsbToastr */

	// rtsbToastr.options.positionClass =
	// 	'toast-' + (rtsbWishlistParams.notice.position || 'center-center');
	$.fn.rtsb_wishlist = function() {
		$( document )
			.off( 'click', '.rtsb-wishlist-add' )
			.off( 'click', '.rtsb-wishlist-remove' )
			.on( 'click', '.rtsb-wishlist-add', function( e ) {
				e.preventDefault();
				const _$self = $( this );
				const product_id = _$self.data( 'product_id' );
				if ( ! product_id ) {
					return;
				}
				$.ajax( {
					type: 'POST',
					headers: {
						'X-WP-Nonce': rtsbWishlistParams.rest_nonce
					},
					url:
						rtsbWishlistParams.resturl +
						'rtsb/v1/wishlist/add_to_list/',
					data: {
						product_id
					},
					beforeSend( jqXhr ) {
						_$self.rtsbBlock();
						$( document ).trigger( 'rtsb.wishlist.adding', [ _$self ] );
					},
					success( res ) {

						let browseTxt = rtsbWishlistParams.notice.added,
							actionTxt;
						if ( rtsbWishlistParams.notice.browse ) {
							browseTxt =
								' <a href="' +
								rtsbWishlistParams.pageUrl +
								'">' +
								rtsbWishlistParams.notice.browse +
								'</a>';
						}
						if ( res.success ) {
							$( document ).trigger( 'rtsb.wishlist.added', [
								res,
								_$self,
							] );
							actionTxt = rtsbWishlistParams.notice.added;
							rtsbToastr.success( browseTxt, actionTxt );
						} else {
							$( document ).trigger( 'rtsb.wishlist.adding_error', [
								res,
								_$self,
							] );
							if ( res.data && res.data.action === 'exist' ) {
								actionTxt = rtsbWishlistParams.notice.exist;
								rtsbToastr.success( browseTxt, actionTxt );
							} else {
								rtsbToastr.error( res.message );
							}
						}
						_$self.rtsbUnblock();
					},
					error( err ) {
						// console.log( err );
						$( document ).trigger( 'rtsb.wishlist.adding_error', [
							err,
							_$self,
						] );
						_$self.rtsbUnblock();
						// if( status){
						//
						// }
						if( 'authentication_error' === err.responseJSON.code ) {
							// console.log( err.responseJSON );
							rtsbToastr.error( err.responseJSON.message );
						} else {
							rtsbToastr.error( rtsbWishlistParams.notice.error );
						}
					},
					complete( res ) {
						$( document ).trigger( 'rtsb.wishlist.adding_completed', [
							res,
							_$self,
						] );
					},
				} );
			} )
			.on( 'click', '.rtsb-wishlist-remove', function( e ) {
				e.preventDefault();
				const _$self = $( this );
				const product_id = _$self.data( 'product_id' );
				if ( ! product_id ) {
					return;
				}
				$.ajax( {
					type: 'POST',
					headers: {
						'X-WP-Nonce': rtsbWishlistParams.rest_nonce
					},
					url:
						rtsbWishlistParams.resturl +
						'rtsb/v1/wishlist/remove_from_list/',
					data: {
						product_id
					},
					beforeSend( jqXhr ) {
						_$self.rtsbBlock();
						$( document ).trigger( 'rtsb.wishlist.removing', [ _$self ] );
					},
					success( res ) {
						if ( res.success ) {
							$( document ).trigger( 'rtsb.wishlist.removed', [
								res,
								_$self,
							] );
							rtsbToastr.success(
								rtsbWishlistParams.notice.removed
							);
						} else {
							$( document ).trigger(
								'rtsb.wishlist.removing_error',
								[ res, _$self ]
							);
							rtsbToastr.error( res.message );
						}
						_$self.rtsbUnblock();
					},
					error( err ) {
						$( document ).trigger( 'rtsb.wishlist.removing_error', [
							err,
							_$self,
						] );
						_$self.rtsbUnblock();
						rtsbToastr.error( rtsbWishlistParams.notice.error );
					},
					complete( res ) {
						$( document ).trigger(
							'rtsb.wishlist.removing_completed',
							[ res, _$self ]
						);
					},
				} );
			} );
	};
	$( document )
		.on( 'rtsb.wishlist.added', function( e, res, $item ) {
			$item
				.removeClass( 'rtsb-wishlist-add' )
				.addClass( 'rtsb-wishlist-remove' );
			$( '.rtsb-wishlist-counter' ).text( res.data.item_count );
			$item.attr( 'original-title', rtsbWishlistParams.notice.remove_wishlist );
			$($item).find('.button-text').text(rtsbWishlistParams.notice.remove_wishlist);

		} )
		.on( 'rtsb.wishlist.removed', function( e, res, $item ) {
			$item
				.removeClass( 'rtsb-wishlist-remove' )
				.addClass( 'rtsb-wishlist-add' )
				.parents(
					'tr.rtsb-wl-product[data-product_id="' +
						res.data.product_id +
						'"]'
				)
				.slideUp( 'slow', function() {
					$( this ).remove();
				} );
			$( '.rtsb-wishlist-counter' ).text( res.data.item_count );
			$item.attr( 'original-title', rtsbWishlistParams.notice.add_to_wishlist );
			$($item).find('.button-text').text(rtsbWishlistParams.notice.button_text);
		} );
	$( function() {
		$.fn.rtsb_wishlist();
	} );
}( jQuery ) );
